@import '../variables.scss';

// standard appearance
.mat-form-field-appearance-standard {
  width: 100%;
  height: calc(86px - 1.4375em);

  .mat-form-field-wrapper {
    padding-bottom: 0;
  }

  .mat-form-field-underline {
    bottom: 0;
  }

  .mat-form-field-suffix {
    .mat-icon-button {
      top: 6px;
      width: 30px !important;
      height: 30px !important;
      line-height: 30px;
    }

    .entity-dropdown-button {
      cursor: text;

      &.mat-button-disabled {
        cursor: default;
      }
    }

    .selection-suffix {
      display: flex;

      .mat-icon-button {
        top: 0;

        .mat-icon {
          font-size: 18px;
        }
      }

      .active {
        background-color: $accent-color;
        color: white;

        &.mat-button-disabled {
          background-color: $editUI-label-disabled-color;
        }
      }
    }
  }

  .mat-select-arrow-wrapper {
    transform: translateX(-5px) !important;

    .mat-select-arrow {
      color: $editUI-foreground-color;
    }
  }
}

// boolean field
.mat-slide-toggle {
  padding-top: 12px;
}
